import React from 'react';
import { Form, Input, Button, message, Row, Col, Select, Rate } from 'antd';
const { Option } = Select;
const Feedback = () => {
  const onFinish = () => {
    message.success('Sent');
  };
  return (
    <div className='feedback'>
      <div className='space'>
        <h3>Feedback</h3>
        <p>Your feedback matters!</p>
        <Form name='basic' onFinish={onFinish}>
          <Row gutter={8}>
            <Col md={14}>
              <Form.Item
                name='name'
                rules={[{ required: true, message: 'Please enter your name!' }]}
              >
                <Input placeholder='Enter Full Name' />
              </Form.Item>
            </Col>
            <Col md={10}>
              <Form.Item
                name='role'
                rules={[
                  {
                    required: true,
                    message: 'Please select a role!',
                  },
                ]}
              >
                <Select placeholder='Select your role'>
                  <Option value='Student'>Student</Option>
                  <Option value='Teacher'>Teacher</Option>
                </Select>
              </Form.Item>
            </Col>
            <Col md={24}>
              <Form.Item
                name='rating'
                className='ratings-field'
                rules={[
                  {
                    required: true,
                    message: 'Rate us out of 5!',
                  },
                ]}
              >
                <Rate defaultValue={3} />
              </Form.Item>
            </Col>
          </Row>

          <Form.Item
            name='aboutIssue'
            rules={[{ required: true, message: 'Write a comment!' }]}
          >
            <Input.TextArea placeholder='Write a comment for us' rows={4} />
          </Form.Item>
          <Form.Item>
            <Button type='primary' htmlType='submit' className='flex-center '>
              Send Feedback
            </Button>
          </Form.Item>
        </Form>
      </div>
    </div>
  );
};

export default Feedback;
